<template>
  <svg v-if="look === 'edge'"
       :class="'edge-' + player"
       ref="container"
       class="edge"
       xmlns="http://www.w3.org/2000/svg"
       width='100%'
       height='100%'
       viewBox="0 0 100 21"
       preserveAspectRatio="xMidYMin meet"
  >
    <polygon :fill="fill"
             :stroke="border"
             stroke-width="3.5"
             points="27,5 73,5 73,15 27,15"
    />
  </svg>

  <svg v-else-if="num === 5 && count > 5"
       :class="'circle-' + player"
       ref="container"
       class="circle"
       xmlns="http://www.w3.org/2000/svg"
       width='100%'
       height='100%'
       viewBox="0 0 100 100"
       preserveAspectRatio="xMidYMin meet"
  >
    <defs>
      <radialGradient :id="'grad' + stepId + num" cx="50%" cy="50%" r="50%" fx="50%" fy="50%">
        <stop offset="0%" :style="{'stop-color': fill}" style="stop-opacity:0.5" />
        <stop offset="100%" :style="{'stop-color': fill}" style="stop-opacity:1" />

      </radialGradient>
    </defs>
    <g>
      <circle cx="50"
              cy="50"
              r="40"
              :fill="hint ? 'url(' + '#grad' + stepId + num + ')' : fill"
              :stroke="hint ? '#00ff00' : border"
              :stroke-width="hint ? 12 : 8"
      />
      <text x="50" y="75" text-anchor="middle" font-family="sans-serif" font-weight="bold" font-size="65" :fill="border">{{ count }}</text>
      <circle cx="50" cy="50" r="40" fill="#ffffff" fill-opacity="0" stroke="#000000" stroke-opacity="0" stroke-width="8"/>
    </g>
  </svg>

  <svg v-else-if="num <= 5"
       :class="'circle-' + player"
       ref="container"
       class="circle"
       xmlns="http://www.w3.org/2000/svg"
       width='100%'
       height='100%'
       viewBox="0 0 100 100"
       preserveAspectRatio="xMidYMid meet"
  >
    <defs>
      <radialGradient :id="'grad' + stepId + num" cx="50%" cy="50%" r="50%" fx="50%" fy="50%">
        <stop offset="0%" :style="{'stop-color': fill}" style="stop-opacity:0.5" />
        <stop offset="100%" :style="{'stop-color': fill}" style="stop-opacity:1" />

      </radialGradient>
    </defs>
    <circle cx="50"
            cy="50"
            r="40"
            :fill="hint ? 'url(' + '#grad' + stepId + num + ')' : fill"
            :stroke="hint ? '#00ff00' : border"
            :stroke-width="hint ? 12 : 8"
    />
  </svg>

</template>

<script>
import { computed, ref, onMounted } from 'vue'

export default {
  props: {
    player: String,
    stepId: Number, // :(
    count: Number,
    num: Number,
    hint: Boolean,
    look: {
      type: String,
      default: 'face'
    }
  },

  setup (props, context) {
    const border = computed(() => props.player === 'white' ? '#000000' : '#ffffff')
    const fill = computed(() => props.player === 'white' ? '#ffffff' : '#000000')

    const container = ref(null)

    onMounted(() => {
      // the DOM element will be assigned to the ref after initial render
      // console.log('CHSVG: ', container.value)
    })

    return { border, fill, container }
  }
}

</script>

<style scoped>
.circle {
  z-index: 1;
  font-size: 0;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 8vh;
}

.edge {
  z-index: 1;
  font-size: 0;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 3vh;
}

.circle-move {

}
</style>
