  /*background-image: url('../assets/triangle_bottom_light.svg');*/
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.bar {
  background-color: #C5873F;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  /*background-image: url('../assets/rectangle_light.svg');*/
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
